.pie-chart-container {
  width: 10vw; /* Adjust width as needed */
  height: 10vw; /* Adjust height as needed */
}

.pie-chart-segment {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: relative;
}

.white-circle {
  width: 80%; /* Adjust size as needed */
  height: 80%; /* Adjust size as needed */
  border-radius: 50%;
  background-color: var(--thrid-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
